@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
#intro1{
    color: white;
    background: palevioletred;
}
#intro2{
    color:white;
    background: purple;
}
#intro3{
    background: blueviolet;
    color: white;
}
#bookGallery{
    background: #272727;
}
#bookImage{
    
    width: inherit !important;
    height: 30vw !important;
    vertical-align: middle;
    background-size: cover !important;


}


  
 .carousel-item {

   transition: 0.2s cubic-bezier(0.2, 0.8, 0.2, 1.0);
}
.carousel-inner{
    border-radius: 8px !important;

    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.8) !important;

}

#titleLogo{
    background: #272727;
    color: white;
}
#LoginButton {
  border-radius: 0;
  color: white;
  border-color: rgb(64, 66, 68);
  background: rgb(64, 66, 68);
}
#LoginButton:focus {
  background: rgb(89, 92, 95);
  color: white;
  box-shadow: 0 0 0 0.2rem rgba(132, 0, 255, 0);
}
#LoginButton:hover {
  color: white;
  background: rgb(89, 92, 95);
  box-shadow: 0 0 0 0.2rem rgba(132, 0, 255, 0);
}
#SignUpButton {
  border-radius: 0;
  color: white;
  border-color: rgba(132, 0, 255, 0);

  background: #720ea4;
  box-shadow: 0 0 0 0.2rem rgba(132, 0, 255, 0);
}
#SignUpButton:focus {
  background: rgb(130, 16, 187);
}
#SignUpButton:hover {
  background: rgb(130, 16, 187);
}
#cartDiv {
  color: black;
  text-align: center;
  font-size: 1.2rem;
  background-color: white;
  padding: 0.2rem;
  border-radius: 0px;
  width: auto;
  border-color: white;
}
#cartDiv:hover {
  background-color: #d1d1d1;
  border-color: #d1d1d1;
}
#cartDiv:focus {
  border-color: #d1d1d1;
  background-color: #d1d1d1;
}

#categorySelect {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
}

.Sidebar {
  padding-top: 20px;
  background-color: #f1f1f1;
  height: 100%;
}
.wrapper {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: stretch;
          align-items: stretch;
}

body {
  font-family: 'Poppins', sans-serif;
  background: #fafafa;
}

p {
  font-family: 'Poppins', sans-serif;
  font-size: 1.1em;
  font-weight: 300;
  line-height: 1.7em;
  color: #999;
}

a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
  transition: all 0.3s;
}

#sidebar {
  background: #7386d5;
  color: #fff;
  transition: all 0.3s;
}

#sidebar .sidebar-header {
  padding: 20px;
  background: #6d7fcc;
}

#sidebar ul.components {
  padding: 20px 0;
}

#sidebar ul p {
  color: #fff;
  padding: 10px;
}

#sidebar ul li a {
  padding: 10px;
  font-size: 1.1em;
  display: block;
}
#sidebar ul li a:hover {
  color: #7386d5;
  background: #fff;
}

#sidebar ul li.active > a,
a[aria-expanded='true'] {
  color: #fff;
  background: #6d7fcc;
}
ul ul a {
  font-size: 0.9em !important;
  padding-left: 30px !important;
  background: #6d7fcc;
}

#footerLink:hover{
    text-decoration: none;
    color: white;
    transition: 0.8s cubic-bezier(0.2, 0.8, 0.2, 1.0);
    transition-property: all;
   
}
#footerLink{
    color: #6c757d;
    transition: 0.2s cubic-bezier(0.2, 0.8, 0.2, 1.0);
    transition-property: all;
    
}
#entireFooter{
    border-top: black;
    border-top-style: solid;
    border-top-width: 1px;
}

html{
    font-family: 'Open Sans', sans-serif;


}
p {
    font-family: 'Open Sans', sans-serif;



}
body {
    font-family: 'Open Sans', sans-serif;

}


